* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

input {
  width: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
}

.Menu .hamburger {
  position: fixed;
  margin: 15px;
  display: inline-block;
  cursor: pointer;
  z-index: 2;
}

.Menu .hamburger .bar1,
.Menu .hamburger .bar2,
.Menu .hamburger .bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.Menu .hamburger.active .bar1,
.Menu .hamburger.active .bar2,
.Menu .hamburger.active .bar3 {
  background: #ddd;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.Menu .hamburger.active .bar1:hover,
.Menu .hamburger.active .bar2:hover,
.Menu .hamburger.active .bar3:hover {
  background: #eee;
}

.Menu .hamburger.active .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
          transform: rotate(-45deg) translate(-9px, 6px);
}

.Menu .hamburger.active .bar2 {
  opacity: 0;
}

.Menu .hamburger.active .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
          transform: rotate(45deg) translate(-8px, -8px);
}

.Menu .overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.85);
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.Menu .overlay.active {
  width: 320px;
}

.Menu .overlay.active a {
  font-size: 24px !important;
}

.Menu .overlay .overlay-content {
  position: relative;
  width: 100%;
  text-align: center;
  margin-top: 50px;
}

.Menu .overlay .overlay-content a {
  padding: 8px;
  text-decoration: none;
  font-size: 0px;
  color: #818181;
  display: block;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.Menu .overlay .overlay-content a:hover,
.Menu .overlay .overlay-content a:focus {
  color: #f1f1f1;
}

.random-word-component, .Alphabetical .random-word, .Discussional .random-word {
  cursor: pointer;
  position: fixed;
  bottom: 15px;
  right: 15px;
  width: 48px;
  padding: 3px;
  border: 1px solid black;
  border-radius: 50%;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.random-word-component:hover, .Alphabetical .random-word:hover, .Discussional .random-word:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

